
















































import Vue from "vue";
import LoadingBar from "../../../components/LoadingBar.vue";
export default Vue.extend({
	components: {
		LoadingBar
	},
	data() {
		return {
			isLoading: false,
			name: "",
			email: "",
			number: "",
			phone: "",
			content: "",
			isModification: false
		};
	},
	created() {
		if (this.getUserInformation.name) {
			this.name = this.getUserInformation.name;
			this.email = this.getUserInformation.email;
		} else {
			this.$router.back();
		}
		if (this.getUserToken) {
			this.$store
				.dispatch("GET_MY_APPLICANT")
				.then(applicant => {
					this.isModification = true;
					this.name = applicant.name;
					this.email = applicant.email;
					this.number = applicant.number;
					this.phone = applicant.phone;
					this.content = applicant.content;
				})
				.catch(err => {});
		}
	},
	watch: {
		getUserToken() {
			if (this.getUserInformation.name) {
				this.name = this.getUserInformation.name;
			}
			if (this.getUserInformation.email) {
				this.email = this.getUserInformation.email;
			}
			this.$store
				.dispatch("GET_MY_APPLICANT")
				.then(applicant => {
					this.isModification = true;
					this.name = applicant.name;
					this.email = applicant.email;
					this.number = applicant.number;
					this.phone = applicant.phone;
					this.content = applicant.content;
				})
				.catch(err => {});
		}
	},
	methods: {
		submit() {
			this.isLoading = true;
			if (this.isModification) {
				this.$store
					.dispatch("APPLICANT_MODIFICATION", {
						name: this.name,
						email: this.email,
						number: this.number,
						phone: this.phone,
						content: this.content
					})
					.then(data => {
						this.isLoading = false;
						console.log(data);
					})
					.catch(err => {
						this.isLoading = false;
						console.log(err);
					});
			} else {
				this.$store
					.dispatch("APPLICANT", {
						name: this.name,
						email: this.email,
						number: this.number,
						phone: this.phone,
						content: this.content
					})
					.then(data => {
						this.isLoading = false;
						console.log(data);
					})
					.catch(err => {
						this.isLoading = false;
						console.log(err);
					});
			}
		}
	},
	computed: {
		getUserInformation() {
			return this.$store.state.userInformation;
		},
		getUserToken() {
			return this.$store.state.userToken;
		},
		getIsModificationText(): string {
			return this.isModification ? "수정" : "작성";
		}
	}
});
