






import Vue from "vue";
import MemberBox from "../../components/Page/MemberBox.vue";
export default Vue.extend({
    name : "Members",
	components: {
		MemberBox
	},
	data() {
		return {
			members: []
		};
	},
	created() {
        this.$store
			.dispatch("GET_CLUB_MEMBERS")
			.then(members => {
				this.members = members;
			})
			.catch(err => {});
	}
});
