






































import Vue from "vue";
import Terms from "../components/Terms.vue";
export default Vue.extend({
    name : "Register",
	components: {
		Terms
	},
	data() {
		return {
			email: "",
			password: "",
			name: ""
		};
	},
	methods: {
		enterPress(e: any) {
			if (e.keyCode == 13) this.register();
		},
		register() {
			this.$store
				.dispatch("REGISTER", {
					email: this.email,
					password: this.password,
					name: this.name
				})
				.then(token => {
					this.$router.push("/login");
				})
				.catch(err => {
					console.log(err);
				});
		}
	}
});
