
























































import Vue from "vue";
export default Vue.extend({
	data() {
		return {
			applicants: [],
			sortKey: "",
			sortBy: false,
			currentApplicant: 0
		};
	},
	created() {
		this.reload();
	},
	methods: {
		orderBy(key: string) {
			if (this.sortKey == key) {
				this.sortBy = !this.sortBy;
			} else {
				this.sortKey = key;
			}
		},
		reload() {
			this.$store
				.dispatch("GET_CLUB_APPLICANT")
				.then(applicants => {
					this.applicants = applicants;
				})
				.catch(err => {});
		},
		accept() {
			this.$store
				.dispatch(
					"APPLICANT_ACCEPT",
					this.applicants[this.currentApplicant]
				)
				.then(applicant => {
					this.reload();
				})
				.catch(err => console.log(err));
		},
		reject() {
			this.$store
				.dispatch(
					"APPLICANT_REJECT",
					this.applicants[this.currentApplicant]
				)
				.then(applicant => {
					this.reload();
				})
				.catch(err => console.log(err));
		}
	},
	computed: {
		getOrderedApplicants(): any {
			if (this.sortKey == "") {
				return this.applicants;
			} else {
				return this.applicants.sort((a: any, b: any): any => {
					if (this.sortBy)
						return b[this.sortKey] > a[this.sortKey] ? 1 : -1;
					else return a[this.sortKey] > b[this.sortKey] ? 1 : -1;
				});
			}
		}
	}
});
