














































import Vue from "vue";
export default Vue.extend({
	data() {
		return {
			item: "",
			size: "",
			price: 0,
			quantity: 0,
			shopping: 0,
			url: "",
			date: null
		};
	},
	created() {},
	methods: {
		create() {
			this.$store
				.dispatch("BUDGET", {
					item: this.item,
					size: this.size,
					price: this.price,
					quantity: this.quantity,
					shopping: this.shopping,
					url: this.url,
					date: this.date
				})
				.then(award => {
					this.$emit("isUpdated", false);
				})
				.catch(err => {});
		}
	},
	computed: {}
});
